/*** 

====================================================================
	Reset
====================================================================

 ***/

* {
	margin     : 0px;
	padding    : 0px;
	border     : none;
	outline    : none;
	font-size  : 100%;
	line-height: inherit;
}

/*** 

====================================================================
	Global Settings
====================================================================

 ***/

body {
	font-size             : 16px;
	color                 : #282828;
	line-height           : 1.7em;
	font-weight           : 400;
	background            : #ffffff;
	-webkit-font-smoothing: antialiased;
	-moz-font-smoothing   : antialiased;
	font-family           : 'Roboto', sans-serif;
}

a {
	text-decoration: none;
	cursor         : pointer;
}

a:hover,
a:focus,
a:visited {
	text-decoration: none;
	outline        : none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	position   : relative;
	font-weight: normal;
	margin     : 0px;
	background : none;
	line-height: 1.25em;
}

textarea {
	overflow: hidden;
}

button {
	outline: none !important;
	cursor : pointer;
}

p,
.text {
	font-size  : 16px;
	line-height: 1.7em;
	font-weight: 400;
	margin     : 0 0 15px;
}

::-webkit-input-placeholder {
	color: inherit;
}

::-moz-input-placeholder {
	color: inherit;
}

::-ms-input-placeholder {
	color: inherit;
}

.page-wrapper {
	position : relative;
	margin   : 0 auto;
	width    : 100%;
	min-width: 300px;
	z-index  : 9;
	overflow : hidden;
}

.auto-container {
	position : static;
	max-width: 1200px;
	padding  : 0px 15px;
	margin   : 0 auto;
}

ul,
li {
	list-style: none;
	padding   : 0px;
	margin    : 0px;
}

figure {
	margin-bottom: 0;
}

.theme-btn {
	display           : inline-block;
	-webkit-transition: all 0.3s ease;
	-o-transition     : all 0.3s ease;
	transition        : all 0.3s ease;
}

.theme-btn i {
	position   : relative;
	display    : inline-block;
	font-size  : 14px;
	margin-left: 3px;
}

.centered {
	text-align: center !important;
}

.gray-bg {
	background-color: #f4f4f4 !important;
}

/*Btn Style One*/

.btn-style-one {
	position        : relative;
	display         : inline-block;
	font-size       : 16px;
	line-height     : 30px;
	color           : #ffffff;
	font-weight     : 600;
	text-transform  : capitalize;
	border-radius   : 30px;
	overflow        : hidden;
	vertical-align  : middle;
	background-color: transparent;
}

.btn-style-one:hover {
	color: #ffffff;
}

.btn-style-one .btn-title {
	position          : relative;
	display           : block;
	z-index           : 1;
	padding           : 15px 45px;
	border-radius     : 30px;
	transition        : all 0.3s ease;
	-moz-transition   : all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition    : all 0.3s ease;
	-o-transition     : all 0.3s ease;
}

.btn-style-one:hover .btn-title {
	color: #ffffff;
}

.btn-style-one .btn-title:before {
	position                          : absolute;
	left                              : 0;
	top                               : 0;
	z-index                           : -1;
	height                            : 100%;
	width                             : 100%;
	content                           : "";
	-webkit-transform                 : scale(0, 1);
	-ms-transform                     : scale(0, 1);
	transform                         : scale(0, 1);
	-webkit-transform-origin          : top right;
	-ms-transform-origin              : top right;
	transform-origin                  : top right;
	-webkit-transition                : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-o-transition                     : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-webkit-transition                : -webkit-transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition                        : -webkit-transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition                        : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition                        : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000), -webkit-transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-webkit-transition-timing-function: cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-o-transition-timing-function     : cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition-timing-function        : cubic-bezier(0.860, 0.000, 0.070, 1.000);
}

.btn-style-one:hover .btn-title:before {
	-webkit-transform       : scale(1, 1);
	-ms-transform           : scale(1, 1);
	transform               : scale(1, 1);
	-webkit-transform-origin: top left;
	-ms-transform-origin    : top left;
	transform-origin        : top left;
}

/* Btn Style Two */

.btn-style-two {
	position      : relative;
	display       : inline-block;
	font-size     : 16px;
	line-height   : 30px;
	color         : #ffffff;
	font-weight   : 600;
	text-transform: capitalize;
	border-radius : 30px;
	overflow      : hidden;
	vertical-align: middle;
}

.btn-style-two:hover {
	color: #ffffff;
}

.btn-style-two .btn-title {
	position          : relative;
	display           : block;
	z-index           : 1;
	padding           : 15px 45px;
	border-radius     : 30px;
	transition        : all 0.3s ease;
	-moz-transition   : all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition    : all 0.3s ease;
	-o-transition     : all 0.3s ease;
}

.btn-style-two:hover .btn-title {
	color: #ffffff;
}

.btn-style-two .btn-title:before {
	position                          : absolute;
	left                              : 0;
	top                               : 0;
	z-index                           : -1;
	height                            : 100%;
	width                             : 100%;
	content                           : "";
	-webkit-transform                 : scale(0, 1);
	-ms-transform                     : scale(0, 1);
	transform                         : scale(0, 1);
	-webkit-transform-origin          : top right;
	-ms-transform-origin              : top right;
	transform-origin                  : top right;
	-webkit-transition                : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-o-transition                     : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-webkit-transition                : -webkit-transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition                        : -webkit-transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition                        : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition                        : transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000), -webkit-transform 500ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-webkit-transition-timing-function: cubic-bezier(0.860, 0.000, 0.070, 1.000);
	-o-transition-timing-function     : cubic-bezier(0.860, 0.000, 0.070, 1.000);
	transition-timing-function        : cubic-bezier(0.860, 0.000, 0.070, 1.000);
}

.btn-style-two:hover .btn-title:before {
	-webkit-transform       : scale(1, 1);
	-ms-transform           : scale(1, 1);
	transform               : scale(1, 1);
	-webkit-transform-origin: top left;
	-ms-transform-origin    : top left;
	transform-origin        : top left;
}

.btn-style-two .btn-title:after {
	position          : absolute;
	left              : 0;
	top               : 0;
	z-index           : -1;
	height            : 100%;
	width             : 100%;
	content           : "";
	-webkit-transform : scale(1);
	-ms-transform     : scale(1);
	transform         : scale(1);
	-webkit-transition: .5s ease;
	-o-transition     : .5s ease;
	transition        : .5s ease;
	border            : 2px solid rgba(248, 248, 248, 0.3);
	border-radius     : 30px;
}

.btn-style-two:hover .btn-title:after {
	-webkit-transform: scale(0.8);
	-ms-transform    : scale(0.8);
	transform        : scale(0.8);
	opacity          : 0;
}

.pull-right {
	float: right;
}

.pull-left {
	float: left;
}

.preloader {
	position           : fixed;
	left               : 0px;
	top                : 0px;
	width              : 100%;
	height             : 100%;
	z-index            : 999;
	background-position: center center;
	background-repeat  : no-repeat;
	background-image   : url(../images/icons/preloader.svg);
}

.style-two .preloader {
	background-image: url(../images/icons/preloader-2.svg);
}

.style-three .preloader {
	background-image: url(../images/icons/preloader-3.svg);
}

.preloader-close {
	position   : fixed;
	z-index    : 999999;
	color      : #fff;
	padding    : 10px 20px;
	cursor     : pointer;
	right      : 0;
	bottom     : 0;
	font-weight: 600;
}

.loader-wrap {
	position: fixed;
	left    : 0px;
	top     : 0px;
	width   : 100%;
	height  : 100%;
	z-index : 999999;
}

.loader-wrap .layer-one {
	position: absolute;
	left    : 0%;
	top     : 0;
	width   : 33.3333%;
	height  : 100%;
	overflow: hidden;
}

.loader-wrap .layer-two {
	position: absolute;
	left    : 33.3333%;
	top     : 0;
	width   : 33.3333%;
	height  : 100%;
	overflow: hidden;
}

.loader-wrap .layer-three {
	position: absolute;
	left    : 66.6666%;
	top     : 0;
	width   : 33.3333%;
	height  : 100%;
	overflow: hidden;
}

.loader-wrap .layer .overlay {
	position  : absolute;
	left      : 0;
	top       : 0;
	width     : 100%;
	height    : 100%;
	background: #0a273d;
}

img {
	display  : inline-block;
	max-width: 100%;
	height   : auto;
}

.dropdown-toggle::after {
	display: none;
}

.fa {
	line-height: inherit;
}

/*** 

====================================================================
	Scroll To Top style
====================================================================

***/

.scroll-to-top {
	position          : fixed;
	right             : 20px;
	bottom            : 20px;
	width             : 54px;
	height            : 54px;
	color             : #ffffff;
	font-size         : 30px;
	line-height       : 54px;
	text-align        : center;
	z-index           : 100;
	cursor            : pointer;
	background        : #25283a;
	border-radius     : 50%;
	margin-left       : -26px;
	display           : none;
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.scroll-to-top:hover {
	color: #ffffff;
}

