/* Side Menu */

.side-menu {
	position     : fixed;
	left         : 0;
	top          : 0;
	width        : 320px;
	padding-right: 30px;
	max-width    : 100%;
	height       : 100%;
	z-index      : 999;
	background   : #222;
	transform    : translateX(-320px);
	transition   : .5s ease;
}

.side-menu .side-menu-resize {
	position        : absolute;
	right           : -40px;
	top             : 50%;
	height          : 40px;
	width           : 40px;
	color           : #fff;
	background-color: #3e3e3e;
	display         : none;
	z-index         : 9;
	text-align      : center;
	line-height     : 40px;
	cursor          : pointer;
	margin-top      : -20px;
	transition      : .5s ease;
}

.side-menu-visible .side-menu .side-menu-resize {
	right     : 0;
	background: rgba(255, 255, 255, 0.1);
}

.side-menu-visible .side-menu .side-menu-resize .fa-angle-right:before {
	content: "\f104";
}

@media only screen and (min-width: 1400px) {
	.side-menu-visible .side-menu {
		transform: translateX(0);
	}

	.side-menu .side-menu-resize {
		display: block;
	}

	.side-menu-visible {
		padding-left: 320px;
		transition  : .5s;
	}

	.main-header .mobile-menu-two {
		display: none;
	}

	.side-menu-overlay {
		display: none;
	}
}

@media only screen and (max-width: 1399px) {
	.side-menu-visible-s2 .side-menu {
		transform: translateX(0);
	}

	.side-menu-overlay {
		position  : fixed;
		top       : 0;
		left      : 0;
		right     : 0;
		bottom    : 0;
		background: rgba(34, 34, 34, 0.45);
		z-index   : 99;
		cursor    : -webkit-zoom-out;
		opacity   : 0;
		visibility: hidden;
	}

	.side-menu-visible-s2 .side-menu-overlay {
		opacity   : 1;
		visibility: visible;
	}
}

