	content           : '';
	left              : 0;
	top               : 0;
	width             : 100%;
	height            : 100%;
	background-color  : #fff;
	opacity           : 0;
	-webkit-transition: .5s;
	-o-transition     : .5s;
	transition        : .5s;
	-webkit-transform : translateX(-80px);
	-ms-transform     : translateX(-80px);
	transform         : translateX(-80px);
}

.gallery-block-one .inner-box:hover .caption-title:before {
	opacity          : 1;
	-webkit-transform: translateX(0);
	-ms-transform    : translateX(0);
	transform        : translateX(0);
}

.gallery-block-one .caption-title h5 {
	font-size         : 14px;
	font-weight       : 700;
	padding-left      : 40px;
	margin-bottom     : 10px;
	opacity           : 0;
	-webkit-transition: .5s;
	-o-transition     : .5s;
	transition        : .5s;
}

.gallery-block-one .inner-box:hover .caption-title h5 {
	opacity: 1;
}

.gallery-block-one .caption-title h5:before {
	position                : absolute;
	content                 : '';
	left                    : 0;
	top                     : 7px;
	width                   : 0;
	height                  : 2px;
	-webkit-transition      : .5s;
	-o-transition           : .5s;
	transition              : .5s;
	-webkit-transition-delay: .5s;
	-o-transition-delay     : .5s;
	transition-delay        : .5s;
}

.gallery-block-one .inner-box:hover .caption-title h5:before {
	width: 30px;
}

.gallery-block-one .caption-title h3 {
	font-size               : 24px;
	font-weight             : 700;
	-webkit-transition      : .5s;
	-o-transition           : .5s;
	transition              : .5s;
	-webkit-transform       : translateY(10px);
	-ms-transform           : translateY(10px);
	transform               : translateY(10px);
	opacity                 : 0;
	-webkit-transition-delay: .5s;
	-o-transition-delay     : .5s;
	transition-delay        : .5s;
}

.gallery-block-one .inner-box:hover .caption-title h3 {
	opacity          : 1;
	-webkit-transform: translateY(0px);
	-ms-transform    : translateY(0px);
	transform        : translateY(0px);
}

.gallery-block-one .caption-title h3 a {
	color: #282828;
}

.gallery-block-one .caption-title .view-project {
	position                : absolute;
	right                   : 0;
	top                     : 0;
	text-align              : center;
	-webkit-transition      : .5s;
	-o-transition           : .5s;
	transition              : .5s;
	-webkit-transition-delay: .2s;
	-o-transition-delay     : .2s;
	transition-delay        : .2s;
	-webkit-transform       : translateX(40px);
	-ms-transform           : translateX(40px);
	transform               : translateX(40px);
}

.gallery-block-one .inner-box:hover .caption-title .view-project {
	-webkit-transform: translateX(0);
	-ms-transform    : translateX(0);
	transform        : translateX(0);
}

.gallery-block-one .caption-title .view-project a {
	color      : #fff;
	width      : 40px;
