/* Video Section */

.video-section {
	position       : relative;
	padding        : 120px 0 70px;
	background-size: cover;
}

.video-section:before {
	position  : absolute;
	content   : '';
	background: #282828;
	left      : 0;
	top       : 0;
	right     : 0;
	bottom    : 0;
	opacity   : .9;
}

.video-section.style-two:before {
	display: none;
}

.video-section.style-two .default-video-box:before {
	border: 2px solid rgba(40, 40, 40, 0.08);
}

.video-section.style-two .default-video-box:after {
	border: 2px solid rgba(41, 41, 41, 0.08);
}

.video-section.style-two .default-video-box.text-center {
	margin-top: 100px;
}

.default-video-box {
	position     : relative;
	height       : 100px;
	width        : 100px;
	line-height  : 100px;
	text-align   : center;
	border-radius: 50%;
	font-size    : 20px;
}

.default-video-box.text-center {
	margin: 0 auto;
}

.default-video-box:before {
	position     : absolute;
	content      : '';
	height       : 190px;
	width        : 191px;
	top          : -45px;
	left         : -45px;
	border-radius: 50%;
	border       : 2px solid rgba(255, 255, 255, 0.12);
}

.default-video-box:after {
	position     : absolute;
	content      : '';
	height       : 140px;
	width        : 140px;
	top          : -20px;
	left         : -20px;
	border-radius: 50%;
	border       : 2px solid rgba(255, 255, 255, 0.12);
}

.default-video-box a {
	position          : relative;
	z-index           : 5;
	color             : #fff;
	width             : 100px;
	height            : 100px;
	display           : block;
	border-radius     : 50%;
	-webkit-transition: .5s;
	-o-transition     : .5s;
	transition        : .5s;
}

.default-video-box a:before {
	position          : absolute;
	content           : '';
	left              : 0;
	top               : 0;
	right             : 0;
	bottom            : 0;
	border-radius     : 50%;
	opacity           : 1;
	-webkit-transform : scale(1);
	-ms-transform     : scale(1);
	transform         : scale(1);
	-webkit-transition: .5s ease;
	-o-transition     : .5s ease;
	transition        : .5s ease;
}

.default-video-box a:hover:before {
	-webkit-transform: scale(.2);
	-ms-transform    : scale(.2);
	transform        : scale(.2);
	opacity          : 0;
}

.default-video-box a:hover {
	background-color: #fff;
}

.default-video-box a span {
	position: relative;
	z-index : 9;
}

.default-video-box:hover:before {
	-webkit-box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.09);
	box-shadow        : 0 0 0 0 rgba(255, 255, 255, 0.09);
	-webkit-animation : ripple 1s infinite;
	animation         : ripple 3s infinite;
}

.video-section .default-video-box.text-center {
	margin-bottom: 90px;
	margin-top   : 30px;
}

.video-play-button:before {
	content          : "";
	position         : absolute;
	z-index          : 0;
	left             : 50%;
	top              : 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	-ms-transform    : translateX(-50%) translateY(-50%);
	transform        : translateX(-50%) translateY(-50%);
	display          : block;
	width            : 80px;
	height           : 80px;
	background       : #ba1f24;
	border-radius    : 50%;
	-webkit-animation: pulse-border 1500ms ease-out infinite;
	animation        : pulse-border 1500ms ease-out infinite;
}

.video-play-button:after {
	content           : "";
	position          : absolute;
	z-index           : 1;
	left              : 50%;
	top               : 50%;
	-webkit-transform : translateX(-50%) translateY(-50%);
	-ms-transform     : translateX(-50%) translateY(-50%);
	transform         : translateX(-50%) translateY(-50%);
	display           : block;
	width             : 80px;
	height            : 80px;
	background        : #fa183d;
	border-radius     : 50%;
	-webkit-transition: all 200ms;
	-o-transition     : all 200ms;
	transition        : all 200ms;
}

.video-play-button:hover:after {
	background-color: darken(#fa183d, 10%);
}

.video-play-button img {
	position : relative;
	z-index  : 3;
	max-width: 100%;
	width    : auto;
	height   : auto;
}

.video-play-button span {
	display      : block;
	position     : relative;
	z-index      : 3;
	width        : 0;
	height       : 0;
	border-left  : 32px solid #fff;
	border-top   : 22px solid transparent;
	border-bottom: 22px solid transparent;
}

@-webkit-keyframes pulse-border {
	0% {
		-webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
		transform        : translateX(-50%) translateY(-50%) translateZ(0) scale(1);
		opacity          : 1;
	}

	100% {
		-webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
		transform        : translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
		opacity          : 0;
	}
}

@keyframes pulse-border {
	0% {
		-webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
		transform        : translateX(-50%) translateY(-50%) translateZ(0) scale(1);
		opacity          : 1;
	}

	100% {
		-webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
		transform        : translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
		opacity          : 0;
	}
}

