/* Feature Section */

.feature-section {
	position: relative;
}

.feature-section .wrapper-box {
	position  : relative;
	margin-top: -86px;
	z-index   : 9;
}

.feature-block-one {
	position: relative;
}

.feature-block-one .inner-box {
	position          : relative;
	background        : #fff;
	padding           : 34px 20px;
	-webkit-box-shadow: 0px 8px 16px 0px rgba(40, 40, 40, 0.1);
	box-shadow        : 0px 8px 16px 0px rgba(40, 40, 40, 0.1);
	text-align        : center;
	-webkit-transition: .5s;
	-o-transition     : .5s;
	transition        : .5s;
	margin            : 0 auto;
	margin-bottom     : 30px;
	max-width         : 170px;
}

.feature-block-one .inner-box:hover {
	border-radius: 50%;
}

.feature-block-one .icon {
	position     : relative;
	font-size    : 70px;
	line-height  : 70px;
	margin-bottom: 15px;
}

.feature-block-one h5 {
	font-size  : 14px;
	font-weight: 700;
}

/* Testimonial Section */

.testimonial-section {
	position       : relative;
	padding        : 120px 0 90px;
	background-size: cover;
}

.testimonial-block {
	position: relative;
	margin  : 0 15px;
}

.testimonial-block .inner-box {
	position          : relative;
	background        : #fff;
	padding           : 30px;
	margin-bottom     : 30px;
	-webkit-box-shadow: 0px 8px 16px 0px rgba(40, 40, 40, 0.14);
	box-shadow        : 0px 8px 16px 0px rgba(40, 40, 40, 0.14);
}

.testimonial-block .inner-box:before {
	position        : absolute;
	content         : '';
	left            : 50px;
	bottom          : -20px;
	border          : 10px solid transparent;
	border-top-color: #fff;
	width           : 20px;
	height          : 20px;
}

.testimonial-block .icon {
	position   : absolute;
	right      : 0;
	top        : 0;
	font-size  : 150px;
	line-height: 150px;
	color      : #f3f3f3;
}

.testimonial-block .quote {
	position     : relative;
	font-size    : 20px;
	color        : #ffffff;
	height       : 50px;
	width        : 50px;
	line-height  : 50px;
	text-align   : center;
	border-radius: 50%;
	margin-bottom: 10px;
}

.testimonial-block .inner-box h4 {
	font-size    : 20px;
	margin-bottom: 21px;
	font-weight  : 700;
	text-align   : right;
	margin-top   : -40px;
}

.testimonial-block .inner-box .text {
	position     : relative;
	margin-bottom: 0px;
}

.testimonial-block .author-info {
	position    : relative;
	padding-left: 75px;
	min-height  : 70px;
	padding-top : 7px;
	margin-left : 30px;
}

.testimonial-block .thumb {
	position: absolute;
	left    : 0;
	top     : 0;
}

.testimonial-block .thumb img {
	position     : relative;
	width        : auto;
	border-radius: 50%;
}

.testimonial-block .author-info h4 {
	font-size  : 24px;
	font-weight: 700;
}

.testimonial-block .author-info .designation {
	position   : relative;
	font-size  : 16px;
	font-weight: 700;
	line-height: 1.2em;
}

.testimonial-section .owl-theme .owl-nav {
	position: absolute;
	top     : -90px;
	left    : 0;
	right   : 0;
	margin  : 0 auto;
	width   : 100%;
}

.testimonial-section .slick-prev {
	position          : absolute;
	right             : 70px;
	top               : -60px;
	left: auto;
	height            : 60px;
	width             : 60px;
	border-radius     : 50%;
	border            : none;
	text-align        : center;
	line-height       : 56px;
	font-size         : 0px;
	opacity           : 1;
	margin-top        : -30px;
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.testimonial-section .slick-prev:before {
	font-family       : "Flaticon";
	content           : "\f105";
	position          : absolute;
	top               : 0;
	width             : 60px;
	height            : 60px;
	line-height       : 60px;
	left              : 0;
	border-radius     : 50%;
	background        : #fff;
	color             : #282828;
	font-size         : 24px;
	-webkit-transform : rotate(180deg);
	-ms-transform     : rotate(180deg);
	transform         : rotate(180deg);
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.testimonial-section .slick-prev:hover:before {
	color: #000;
}

.testimonial-section .slick-next {
	position          : absolute;
	right             : 0;
	top               : -60px;
	height            : 60px;
	width             : 60px;
	border-radius     : 50%;
	border            : none;
	text-align        : center;
	line-height       : 60px;
	color             : rgba(0, 0, 0, 0);
	font-size         : 0px;
	opacity           : 1;
	margin-top        : -30px;
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.testimonial-section .slick-next:before {
	font-family       : "Flaticon";
	content           : "\f105";
	position          : absolute;
	top               : 0;
	width             : 60px;
	height            : 60px;
	line-height       : 60px;
	right             : 0;
	color             : #282828;
	background        : #fff;
	border-radius     : 50%;
	font-size         : 24px;
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.testimonial-section .slick-next:hover:before {
	color: #000;
}

/* Style Two */

.testimonial-section.style-two:before {
	position: absolute;
	content : '';
	top     : 0;
	left    : 0;
	right   : 0;
	bottom  : 0;
	opacity : .9;
}

.testimonial-section.style-two .sec-title .sub-title {
	color                  : #fff;
	-webkit-text-fill-color: inherit;
}

.testimonial-section.style-two .sec-title .sub-title:before {
	background: #fff;
}

.testimonial-section.style-two .testimonial-block .author-info h4 {
	color: #fff;
}

.testimonial-section.style-two .testimonial-block .author-info .designation {
	color                  : #fff;
	-webkit-text-fill-color: inherit;
}

.testimonial-section.style-two .owl-theme .owl-nav .owl-prev:before {
	background: transparent;
	color     : #fff;
	border    : 1px solid rgba(255, 255, 255, 0.6);
}

.testimonial-section.style-two .owl-theme .owl-nav .owl-next:before {
	color     : #fff;
	border    : 1px solid rgba(255, 255, 255, 0.6);
	background: transparent;
}

.testimonial-section.style-two .owl-theme .owl-nav .owl-prev:hover:before,
.testimonial-section.style-two .owl-theme .owl-nav .owl-next:hover:before {
	background: #fff;
}

