/* Funfacts Section */

.funfacts-section {
	position       : relative;
	padding        : 130px 0 100px;
	background-size: cover;
}

.funfacts-section:before {
	position        : absolute;
	content         : '';
	background-color: #131313;
	left            : 0;
	top             : 0;
	right           : 0;
	bottom          : 0;
	opacity         : .9;
}

.fact-counter-two {
	position: relative;
}

.fact-counter-two .content {
	position    : relative;
	padding-left: 115px;
	min-height  : 100px;
}

.fact-counter-two .icon {
	position: absolute;
	left    : 0;
	top     : 10px;
}

.fact-counter-two .count-box {
	font-size    : 30px;
	font-weight  : 700;
	color        : #fff;
	margin-bottom: 5px;
}

.fact-counter-two .inner {
	width        : -webkit-max-content;
	width        : -moz-max-content;
	width        : max-content;
	margin       : 0 auto;
	margin-bottom: 30px;
}

.fact-counter-two .count-text {
	font-size     : 90px;
	line-height   : 80px;
