/* Case Section */

.cases-section {
	position: relative;
	padding : 110px 0 90px;
}

.cases-section.style-two {
	background-color: #fff;
}

.cases-section .sec-bg {
	position       : absolute;
	left           : 0;
	top            : 0;
	width          : 100%;
	height         : 620px;
	background-size: cover;
}

.cases-section .sec-bg:before {
	position        : absolute;
	content         : '';
	background-color: #282828;
	left            : 0;
	top             : 0;
	width           : 100%;
	height          : 100%;
	opacity         : .9;
}

.cases-section.style-two .case-tabs .case-tab-btns .case-tab-btn {
	color: #fff;
}

.cases-section .auto-container {
	max-width: 920px;
}

.cases-section .case-tab-wrapper {
	margin: 0 0px;
}

.cases-section .owl-carousel .owl-stage-outer {
	overflow: visible;
}

.case-tabs {
	position: relative;
}

.case-tabs .tabs-header {
	position     : relative;
	margin-bottom: 40px;
}

.case-tabs .case-tab-btns {
	position         : relative;
	margin-right     : 0px;
	display          : -webkit-box;
	display          : -ms-flexbox;
	display          : flex;
	-webkit-box-align: center;
	-ms-flex-align   : center;
	align-items      : center;
	-webkit-box-pack : center;
	-ms-flex-pack    : center;
	justify-content  : center;
	-ms-flex-wrap    : wrap;
	flex-wrap        : wrap;
	margin-bottom    : 40px;
}

.case-tabs .case-tab-btns .case-tab-btn {
	position          : relative;
	line-height       : 24px;
	cursor            : pointer;
	font-weight       : 700;
	font-size         : 16px;
	padding           : 13px 20px;
	margin            : 0 5px;
	text-transform    : capitalize;
	transition        : all 0.3s ease;
	-moz-transition   : all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition    : all 0.3s ease;
	-o-transition     : all 0.3s ease;
}

.case-tabs .case-tab-btns .case-tab-btn span {
	position: relative;
}

.case-tabs .case-tab-btns .case-tab-btn:before {
	position          : absolute;
	content           : '';
	width             : 100%;
	height            : 100%;
	left              : 0;
	top               : 0;
	opacity           : 0;
	-webkit-transition: .5s ease;
	-o-transition     : .5s ease;
	transition        : .5s ease;
	border-radius     : 25px;
}

.case-tabs .case-tab-btns .case-tab-btn:last-child {
	margin-right: 0px;
}

.case-tabs .case-tab-btns .case-tab-btn.active-btn {
	color: #fff;
}

.case-tabs .case-tab-btns .case-tab-btn.active-btn:before {
	opacity: 1;
}

.case-tabs .case-tabs-content {
	position: relative;
	display : block;
}

.case-tabs .case-tab {
	position  : absolute;
	left      : 0px;
	top       : 0px;
	width     : 100%;
	height    : auto;
	visibility: hidden;
}

.case-tabs .case-tab.active-tab {
	position  : relative;
	visibility: visible;
	z-index   : 5;
}

.case-tabs .case-block-one .inner-box {
	-webkit-transform : scaleX(0);
	-ms-transform     : scaleX(0);
	transform         : scaleX(0);
	transition        : all 0.7s ease;
	-moz-transition   : all 0.7s ease;
	-webkit-transition: all 0.7s ease;
	-ms-transition    : all 0.7s ease;
	-o-transition     : all 0.7s ease;
}

.case-tabs .active-tab .case-block-one .inner-box {
	-webkit-transform: scaleX(1);
	-ms-transform    : scaleX(1);
	transform        : scaleX(1);
}

.case-block-one {
	position: relative;
	padding : 0 25px;
}

.case-block-one .overlay {
	position               : absolute;
	left                   : 50px;
	bottom                 : 50px;
	right                  : 50px;
	background             : #fff;
	padding                : 46px 50px;
	-webkit-transition     : 1s ease;
	-o-transition          : 1s ease;
	transition             : 1s ease;
	-webkit-transform-style: preserve-3d;
	transform-style        : preserve-3d;
	/* -webkit-transform      : perspective(1200px) rotateX(-90deg) translateZ(-51px);
	transform              : perspective(1200px) rotateX(-90deg) translateZ(-51px); */
	/* opacity                : 0; */
}

.owl-item.active .case-block-one .overlay {
	-webkit-transform       : perspective(1200px) rotateX(0deg) translateZ(0px);
	transform               : perspective(1200px) rotateX(0deg) translateZ(0px);
	-webkit-transition-delay: 0.3s;
	-o-transition-delay     : 0.3s;
	transition-delay        : 0.3s;
	opacity                 : 1;
}

.case-block-one .overlay h5 {
	font-size    : 16px;
	font-weight  : 700;
	margin-bottom: 10px;
	color        : #fff;
}

.case-block-one .overlay h2 {
	font-size  : 30px;
	font-weight: 700;
	color      : #fff;
}

.case-block-one .overlay .link-btn {
	position: absolute;
	right   : 50px;
	top     : 50px;
}

.case-block-one .overlay .link-btn a {
	height     : 60px;
	width      : 60px;
	text-align : center;
	border     : 2px solid #fff;
	line-height: 56px;
	color      : #fff;
	font-weight: 700;
}

.case-block-one .overlay .link-btn a i {
	font-style: normal;
	font-size : 20px;
	margin    : 0;
}

.cases-section .owl-theme .owl-nav {
	position: absolute;
	top     : 50%;
	left    : 0;
	right   : 0;
	margin  : 0 auto;
	width   : 100%;
}

.cases-section .slick-prev {
	position          : absolute;
	left              : 0px;
	top               : 0;
	background        : rgba(255, 255, 255, 0.15);
	height            : 56px;
	width             : 56px;
	border-radius     : 50%;
	border            : none;
	text-align        : center;
	color             : rgba(0, 0, 0, 0);
	line-height       : 56px;
	font-size         : 0px;
	opacity           : 1;
	margin-top        : -30px;
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.cases-section .slick-prev:before {
	font-family       : "Flaticon";
	content           : "\f105";
	position          : absolute;
	top               : 0;
	width             : 56px;
	height            : 56px;
	line-height       : 56px;
	left              : 0;
	border-radius     : 50%;
	background        : #ffffff;
	color             : #282828;
	font-size         : 24px;
	-webkit-transform : rotate(180deg);
	-ms-transform     : rotate(180deg);
	transform         : rotate(180deg);
	-webkit-box-shadow: 0px 16px 32px 0px rgba(165, 165, 165, 0.4);
	box-shadow        : 0px 16px 32px 0px rgba(165, 165, 165, 0.4);
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.cases-section .slick-prev:hover:before {
	color: #fff;
}

.cases-section .slick-next {
	position          : absolute;
	right             : 0px;
	top               : 0;
	background        : rgba(255, 255, 255, 0.15);
	height            : 56px;
	width             : 56px;
	border-radius     : 50%;
	border            : none;
	text-align        : center;
	line-height       : 56px;
	color             : rgba(0, 0, 0, 0);
	font-size         : 0px;
	opacity           : 1;
	margin-top        : -30px;
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.cases-section .slick-next:before {
	font-family       : "Flaticon";
	content           : "\f105";
	position          : absolute;
	top               : 0;
	width             : 56px;
	height            : 56px;
	line-height       : 56px;
	right             : 0;
	color             : #282828;
	background        : #fff;
	border-radius     : 50%;
	font-size         : 24px;
	-webkit-box-shadow: 0px 16px 32px 0px rgba(165, 165, 165, 0.4);
	box-shadow        : 0px 16px 32px 0px rgba(165, 165, 165, 0.4);
	-webkit-transition: all 300ms ease;
	-o-transition     : all 300ms ease;
	transition        : all 300ms ease;
}

.cases-section .slick-next:hover:before {
	color: #fff;
}

